.modal-wrap {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  -webkit-animation: disappear 3s ease-in;
          animation: disappear 3s ease-in;
}


.modal-wrap .modal {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 25%;
  height: 15%;
  border: 2px solid black;
  box-shadow: 0 0 20px 2px green;
  background-color: green;
  border-radius: 10px;
}

