  font-family: "Josefin Sans", sans-serif;
  font-size: 16px;
  text-align: center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}


@-webkit-keyframes disappear {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}


@keyframes disappear {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
div.contact {
  -webkit-animation: makeVisible 0.5s linear both;
          animation: makeVisible 0.5s linear both;
  height: 100%;
  /* overflow: hidden; */
}

div.contact h1 {
  color: var(--h1);
  font-family: "Josefin Sans", sans-serif;
  font-size: 60px;
  text-align: center;
  text-transform: uppercase;
  margin: 20px 0 40px 20px;
  text-shadow: -1px 0 4px var(--bgc), 0 1px 4px var(--bgc), 1px 0 4px var(--bgc),
    0 -1px 4px var(--bgc), 4px 4px 3px var(--shadow), 6px 6px 6px var(--h2);
}

div.contact .container {
  display: -webkit-flex;
  display: flex;
  height: calc(100% - 60px - 40px);
  /* 60px - font size of h1, 40px margin-bottom of h1 */
}

div.contact .info {
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
  padding: 25px 50px 0;
}

div.contact .info p {
  color: #aaa;
  font-family: "Josefin Sans", sans-serif;
  font-size: 16px;
  text-align: center;
  letter-spacing: 0.5px;
}

div.contact form {
  margin-top: 25px;
}

div.contact input,
div.contact textarea {
  font-family: "Josefin Sans", sans-serif;
  width: 100%;
  margin-bottom: 5%;
  padding: 2%;
  font-size: 15px;
  background-color: var(--h1);
}

div.contact input {
  height: 36px;
}

div.contact textarea {
  height: 120px;
  resize: vertical;
  min-height: 120px;
  max-height: 200px;
}

div.contact form input:nth-last-child(1) {
  font-size: 20px;
  height: 60px;
  background-color: black;
  background-color: var(--red);
  color: var(--h1);
  border: 1px solid var(--h1);
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
}

div.contact form input:nth-last-child(1):hover {
  color: #111;
  background-color: var(--h1);
  /* background-color: var(--h2); */
}




/*---- */
div.contact .map {
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
  height: 100%;
}

div.contact .map p {
  color: var(--bgc);
  font-family: "Josefin Sans", sans-serif;
  font-size: 20px;

}

/* MAP */

.mapboxgl-popup-content {
  background-color: var(--h1) !important;
}

.mapboxgl-popup-close-button {
  display: none;
  font-size: 20px;
}
div.logo h3.logo {
  position: absolute;
  top: 25%;
  right: 20%;
  font-size: 260px;
  font-family: "Great Vibes", cursive;
  color: var(--h1);
  -webkit-transform: rotate(-15deg);
          transform: rotate(-15deg);
  text-shadow: 0 3px 3px var(--red), 3px 0 3px var(--red);
  /* animation: shine 2s ease-in-out infinite alternate,
    makeVisible 3s 2s ease-in both; */
  /* animation: shine 2s ease-in-out both, makeVisible 3s 2s ease-in both; */
  -webkit-animation: shine 2s cubic-bezier(0.85, 0, 0.15, 1) infinite alternate,
    makeVisible 1s 0.5s ease-in both;
          animation: shine 2s cubic-bezier(0.85, 0, 0.15, 1) infinite alternate,
    makeVisible 1s 0.5s ease-in both;
  -webkit-transition: 0.6s;
  transition: 0.6s;
}

@-webkit-keyframes shine {
  0% {
    color: var(--h1);
    -webkit-transform: rotate(-15deg) scale(1);
            transform: rotate(-15deg) scale(1);
    text-shadow: 0 3px 3px var(--red), 3px 0 3px var(--red);
  }

  50% {
    /* color: green; */
  }

  100% {
    color: var(--red);
    -webkit-transform: rotate(-15deg) scale(1.05);
            transform: rotate(-15deg) scale(1.05);
    text-shadow: 0 3px 3px var(--h1), 3px 0 3px var(--h1);
  }
}

@keyframes shine {
  0% {
    color: var(--h1);
    -webkit-transform: rotate(-15deg) scale(1);
            transform: rotate(-15deg) scale(1);
    text-shadow: 0 3px 3px var(--red), 3px 0 3px var(--red);
  }

  50% {
    /* color: green; */
  }

  100% {
    color: var(--red);
    -webkit-transform: rotate(-15deg) scale(1.05);
            transform: rotate(-15deg) scale(1.05);
    text-shadow: 0 3px 3px var(--h1), 3px 0 3px var(--h1);
  }
}

