div.contact {
  animation: makeVisible 0.5s linear both;
  height: 100%;
  /* overflow: hidden; */
}

div.contact h1 {
  color: var(--h1);
  font-family: "Josefin Sans", sans-serif;
  font-size: 60px;
  text-align: center;
  text-transform: uppercase;
  margin: 20px 0 40px 20px;
  text-shadow: -1px 0 4px var(--bgc), 0 1px 4px var(--bgc), 1px 0 4px var(--bgc),
    0 -1px 4px var(--bgc), 4px 4px 3px var(--shadow), 6px 6px 6px var(--h2);
}

div.contact .container {
  display: flex;
  height: calc(100% - 60px - 40px);
  /* 60px - font size of h1, 40px margin-bottom of h1 */
}

div.contact .info {
  flex-basis: 50%;
  padding: 25px 50px 0;
}

div.contact .info p {
  color: #aaa;
  font-family: "Josefin Sans", sans-serif;
  font-size: 16px;
  text-align: center;
  letter-spacing: 0.5px;
}

div.contact form {
  margin-top: 25px;
}

div.contact input,
div.contact textarea {
  font-family: "Josefin Sans", sans-serif;
  width: 100%;
  margin-bottom: 5%;
  padding: 2%;
  font-size: 15px;
  background-color: var(--h1);
}

div.contact input {
  height: 36px;
}

div.contact textarea {
  height: 120px;
  resize: vertical;
  min-height: 120px;
  max-height: 200px;
}

div.contact form input:nth-last-child(1) {
  font-size: 20px;
  height: 60px;
  background-color: black;
  background-color: var(--red);
  color: var(--h1);
  border: 1px solid var(--h1);
  cursor: pointer;
  transition: .3s;
}

div.contact form input:nth-last-child(1):hover {
  color: #111;
  background-color: var(--h1);
  /* background-color: var(--h2); */
}




/*---- */
div.contact .map {
  flex-basis: 50%;
  height: 100%;
}

div.contact .map p {
  color: var(--bgc);
  font-family: "Josefin Sans", sans-serif;
  font-size: 20px;

}

/* MAP */

.mapboxgl-popup-content {
  background-color: var(--h1) !important;
}

.mapboxgl-popup-close-button {
  display: none;
  font-size: 20px;
}